
import Vue from 'vue'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ConfirmPlugin);
Vue.use(LoadingPlugin);
Vue.use(AlertPlugin);
Vue.use(ToastPlugin);

import http from "@/http";

import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'


export default {
props: ['address'],
directives: {
TransferDom
},
components: {
Cell, XInput, Group, Flexbox, FlexboxItem, XButton, Popup, PopupPicker, Scroller, ViewBox
},
methods: {
updateRegions(){
/*全量加载省市区地址*/
http('region-api/region/getAllRegionsByLevel', {level:3}, {includeApi:true})
.then(function(data){
if(data){
localStorage['areaList'] = JSON.stringify(data);
this.areaList = this.renderAddress(data);
}
}.bind(this));
},
getAllRegions(){
let areaList = this.areaList;
if(areaList.length===0){
let localAreaList = localStorage['areaList'];
let region = localStorage['region'];
if(localAreaList){
/*全量加载省市区地址*/
http('app-common-api/appAdmin/getDataVersions', {queryParam:['region']}, {includeApi:true})
.then(function(data){
if(data.region === region){
localAreaList = JSON.parse(localAreaList);
this.areaList = this.renderAddress(localAreaList);
}else{
localStorage['region'] = data.region;
this.updateRegions();
}
}.bind(this));

}else{
this.updateRegions();
}
}

},
selectAddress(item){
let address = this.addressObj;
if((item.areaId == address.areaId && item.cityId == address.cityId && item.provinceId == address.provinceId) || typeof address.areaId === 'undefined'){
if(this.addressId){
this.addressId = item.addressId;
}
this.$emit('selectAddress', item);
}

},
ok(){
let list = this.list;
let addressId = this.addressId;
let nowAddress = {};
list.map((v,i)=>{
if(v['addressId']===addressId){
nowAddress = v;
}
});
this.$emit('selectAddress', nowAddress);
},
del(index){
event.stopPropagation();

let _this = this;
this.$vux.confirm.show({
title:'删除',
content:'确定要删除该地址吗？',
onCancel () {
console.log('onCancel');
},
onConfirm () {
_this.$vux.loading.show({
text: '删除中...'
});

/*删除*/
http('user-center-server/user/address/delete', {addressId :_this.list[index]['addressId']} )
.then(function(suc){
if(suc){
_this.list.splice(index, 1);
_this.$vux.loading.hide();
_this.$vux.toast.text('删除成功!');
}

}.bind(this));
}
});
},
edit(index){
event.stopPropagation();

this.getAllRegions();
let obj = Object.assign({}, this.list[index]);
obj.area = [obj.provinceId.toString(), obj.cityId.toString(), obj.areaId.toString()];
this.form = obj;
this.showDialog=true;
},
add(){
this.getAllRegions();
this.form = {
consignee:'',
mobile:'',
area:[],
address:''
};
this.showDialog=true;
},
areaChange(value){
if(value && value.length==3){
this.form.area = value;
}
},
onHide(closeType){
if(closeType && this.form.area.length==0){
this.form.area = ["1", "2", "3"]
}
},
saveAddress(){
let form = this.form;
let addressObj = {
provinceId:form.area[0],
cityId:form.area[1],
areaId:form.area[2],
consignee:form.consignee,
mobile:form.mobile,
address:form.address,
};
if(form && form.addressId){
addressObj.addressId = form.addressId;
}
http('user-center-server/user/address/add-update', {model:addressObj})
.then(function(suc){
if(suc){
this.$vux.toast.text(addressObj.addressId?'保存':'新增' + '成功!');
this.showDialog=false;
this.getAddressList();
}
}.bind(this));
},
cancelAddress(){
this.showDialog=false;
},
/*转换地址信息*/
renderAddress(list){
if(list && list.length>0){
return list.map((v,i)=>{
return {value:v['regionId'].toString(), parent:v['parentId'].toString(), name:v['regionName']}
})
}
},
getAddressList(){
http('user-center-server/user/address', {} )
.then(function(suc){
this.list = suc;
}.bind(this));
}
},
mounted(){
console.log(this.addressObj, this.addressId);
this.getAddressList();

},
watch:{
address(newVal){
this.isSelected = true;
this.addressObj = newVal;
this.addressId = newVal.addressId;
}
},
data () {
return {
isSelected:false,
addressObj:{},
addressId:'',
list:[
],
level1:[],
level2:[],
level3:[],
areaList:[],
form:{
consignee:'',
mobile:'',
area:[],
address:''
},
showDialog:false
}
}
}




